<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>{{from=="stock" ?'库存' : '售后'}}管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">{{from=="stock" ?'商品出库' : '返胎出库'}}</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{this.auditreeoutAdd_Get.reborn_outstock_id ? '修改':'新增'}}出库</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="params" ref="TableDataRef">
        <el-form-item label="库存组织" :rules="rules.company_id" prop="company_id" size="mini">
          <el-select v-model="params.company_id" placeholder="请选择" :disabled="!!this.auditreeoutAdd_Get.reborn_outstock_id" @focus="companyListInvok" @change="companyChange" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type== 1?'optionStyle':''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="供应商(厂家)" size="mini" prop="supplier_id" :rules="rules.supplier_id">
          <el-select clearable v-model="params.supplier_id" maxlength="100" :disabled="!!this.auditreeoutAdd_Get.reborn_outstock_id" :filter-method="supplierChange" @focus="supplierListInvok" filterable placeholder="请选择" @change="changeSuppliter">
            <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务日期" size="mini" prop="business_time" :rules="rules.business_time">
          <el-date-picker v-model="params.business_time" type="date" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="所属区域" size="mini" prop="cityList" :rules="rules.areaadress" class="wauto">
          <getAreaList v-if="defaultArea && defaultArea.length" @changeDefaultArea="changeDefaultArea" :defaultArea="defaultArea" />
        </el-form-item>
        <el-form-item label="详细地址" size="mini" prop="address" :rules="rules.address">
          <el-input v-model="params.address"></el-input>
        </el-form-item>
        <el-form-item label="收货人" size="mini" prop="consignee_name" :rules="rules.consignee_name">
          <el-input v-model="params.consignee_name" maxLength="7"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" size="mini" prop="consignee_mobile" :rules="rules.phone">
          <el-input v-model="params.consignee_mobile"></el-input>
        </el-form-item>
        <el-form-item label="库存部门" size="mini">
          {{(function(){if(params.department_id == 0)params.department_id = null})()}}
          <el-select clearable v-model="params.department_id" @focus="DepartmentListInvok" :disabled="!!this.auditreeoutAdd_Get.reborn_outstock_id" placeholder="请选择" filterable>
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="出库类型" size="mini">
          <template>
            <span>返胎出库</span>
          </template>
        </el-form-item>
        <el-form-item label="摘要" size="mini">
          <el-input v-model="params.remark" type="text" maxlength="300"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="quoteOrder" class="addBtn" size="mini">引用单据</el-button>
      <el-button plain icon="el-icon-delete" @click="deletes" size="mini">删行</el-button>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData" max-height="600" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="brand_name" width="150" label="品牌名称">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" width="150" label="商品编号">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" min-width="500" label="商品名称">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" min-width="100" label="年周/批次">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" width="160" label="出库仓库">
          <template slot-scope="scope">
            <el-select v-model="scope.row.warehouseObj" filterable placeholder="请输入" clearable @focus="getWarehouseInfo(scope.row,scope.$index)" @change="warehouseChange(scope.row,scope.$index)" value-key="allocation_id">
              <ul class="customer customer-ul4 customer-ul2">
                <li>仓库</li>
                <li>货位</li>
                <li style="text-align:right">可用数量</li>
                <li style="text-align:right">在库数量</li>
              </ul>
              <el-option v-for="item in scope.row.wareHouselist" :key="item.allocation_id" :label="item.warehouse_name" :value="item">
                <ul class="customer customer-ul4">
                  <li :title="item.warehouse_name">{{ item.warehouse_name }}</li>
                  <li :title="item.allocation_name">{{item.allocation_name}}</li>
                  <li style="text-align:right" :title="item.available_stocks">{{item.available_stocks}}</li>
                  <li style="text-align:right" :title="item.stocks">{{item.stocks}}</li>
                </ul>
              </el-option>
            </el-select>

          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" width="120" label="出库货位">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" width="160" label="返胎数量" align="right">
          <template slot-scope="scope">
            {{scope.row.goods_num=1}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="tire_un" label="胎号" width="180">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property=" " label=" ">
        </el-table-column>
      </el-table>
    </div>
    <appraisalDalog :message="params.supplier_id" :mesCompanyID="params.company_id" @getAlloca="getAllocationList" v-if="salesDocumentsV" />
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">取 消</el-button>
      <el-button type="primary" @click="submit('TableDataRef')">确 定</el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { getData, opthionList } from "common/utils/public_function/commonUtils";
import getAreaList from "common/components/public/getAreaList";
import appraisalDalog from "./authenticate_dalog.vue";
export default {
  name: "aftersaleAuditreeoutAdd",
  data() {
    return {
      authority: {},
      allocationInventoryList: {},
      list: [],
      TableData: [],
      salesDocumentsV: false,
      multipleSelection: [],
      params: {
        cityList: [],
      },
      defaultArea: [0], //默认的区域id
      from: "",
    };
  },
  components: {
    getAreaList, //区域列表
    appraisalDalog, //销售单据
  },
  methods: {
    ...mapActions(["treeoutreeIndexACT"]),
    goBack() {
      this.$router.push({
        path: "/aftersale/treeoutree",
      });
    },
    dongList(val) {
      if (val.length) {
        if (this.params.outstock_type == "1") {
          let batchIdArr = this.TableData.map((item) => {
            return item.company_appraisal_goods_id;
          });
          val.forEach((item) => {
            if (!batchIdArr.includes(item.company_appraisal_goods_id)) {
              this.TableData.push(item);
            } else {
              this.$message({
                message: "此数据已引用",
                type: "error",
              });
            }
          });
        } else {
          let batchIdArr = this.TableData.map((item) => {
            return item.company_appraisal_goods_id;
          });
          val.forEach((item) => {
            if (!batchIdArr.includes(item.company_appraisal_goods_id)) {
              this.TableData.push(item);
            } else {
              this.$message({
                message: "此数据已引用",
                type: "error",
              });
            }
          });
        }
      }
    },
    getAllocationList(index) {
      this.$axios({
        method: "get",
        url: this.$api.newAllocationList,
        params: {
          company_id: this.params.company_id,
          warehouse_id: this.TableData[index].warehouse_id,
          goods_id: this.TableData[index].goods_id,
        },
      }).then((res2) => {
        if (res2.data.code == 200) {
          this.$set(
            this.TableData[index],
            "allocationInventoryList",
            res2.data.data
          );
        }
      });
    },
    // supplier() {
    //   let options = {
    //     company_id: this.params.company_id,
    //     supplier_name: null
    //   };
    //   supplierListOpen(this, options);
    // },
    supplierChange(val) {
      this.supplierListInvok(val);
    },
    submit(formName) {
      let obj = [];
      if (!this.params.supplier_id) {
        this.$message({
          message: "请选择供应商",
          type: "error",
        });
        return false;
      }
      if (!this.params.abstract) this.params.abstract = "";
      this.TableData.forEach((item) => {
        obj.push({
          factory_appraisal_goods_id: item.factory_appraisal_goods_id,
          goods_id: item.goods_id,
          warehouse_id: item.warehouse_id,
          warehouse_name: item.warehouse_name,
          goods_num: item.goods_num,
          allocation_id: item.allocation_id,
          tire_un: item.tire_un,
        });
      });
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let datas = {
            ...this.params,
            goods_json: JSON.stringify(obj),
          };
          let API = this.auditreeoutAdd_Get.reborn_outstock_id
            ? "editRebornOutstock"
            : "addRebornOutstock";
          // 校验对接车郎中
          this.$baseData
            .checkCarDoctor({
              company_id: this.params.company_id,
              company_name: this.params.company_name,
              bill_type: 16,
              bill_id: this.auditreeoutAdd_Get.reborn_outstock_id,
              operation_type: this.auditreeoutAdd_Get.reborn_outstock_id
                ? 2
                : 1,
              goods_info: JSON.stringify(obj),
            })
            .then(() => {
              this.$axMethod[API](datas).then((res) => {
                if (res.data.code == 200) {
                  this.$message({
                    message: res.data.data,
                    type: "success",
                  });
                  this.goBack();
                }
              });
            });
        }
      });
    },
    warehouseChange(row, index) {
      if (Array.isArray(row.wareHouselist)) {
        let arr = [
          "warehouse_id",
          "warehouse_name",
          "allocation_name",
          "allocation_id",
        ].forEach((item) => {
          this.$set(this.TableData[index], item, row.warehouseObj[item]);
        });
      }
    },
    deletes() {
      if (this.multipleSelection.length) {
        let a = new Set(this.TableData);
        let b = new Set(this.multipleSelection);
        let differenceABSet = new Set([...a].filter((x) => !b.has(x)));
        this.TableData = Array.from(differenceABSet);
      } else {
        this.$message({
          message: "请先选择要删除的数据！",
          type: "warning",
        });
      }
    },
    quoteOrder() {
      if (this.params.supplier_id) {
        this.salesDocumentsV = true;
      } else {
        this.$message({
          message: "请选择供应商(厂家)",
          type: "warning",
        });
      }
    },
    getWarehouseInfo(row, index) {
      //获取仓库 或为 可用数量以及在库 数量
      this.$axMethod
        .getBatchInfo({
          company_id: this.params.company_id,
          goods_id: row.goods_id,
        })
        .then((res) => {
          if (res.data.code == "200") {
            this.$set(this.TableData[index], "wareHouselist", res.data.data);
          }
        });
    },
    // companyFocus() {
    //   getCompanyListCommon(this);
    // },
    companyChange(val) {
      this.$set(this.params, "department_id", null);
      this.$set(this.params, "supplier_id", null);
      this.$set(this, "TableData", []);
      // getDepartmentListCommon(this, this.params);
    },
    changeDefaultArea(addressId) {
      this.params.cityList = addressId.join(",");
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    },
    changeSuppliter(val) {
      if (!val) {
        this.params.address = ""; // 初始化详细地址
        this.defaultArea = [0]; // 初始化区域
        return;
      }
      this.$set(this, "TableData", []);
      // getCompanyListCommon(this); //获取组织
      if (val) {
        var associated_supplier_id = this.supplierList.filter(
          (obj) => obj.supplier_id === val
        )[0].associated_supplier_id;
      }
      this.$axMethod
        .getSupplierInfo({
          company_id: this.params.company_id,
          supplier_id: val,
          associated_supplier_id: associated_supplier_id,
        })
        .then((res) => {
          if (res.data.code == 200) {
            if (res.data.data.province_id) {
              this.defaultArea = [
                res.data.data.province_id,
                res.data.data.city_id,
                res.data.data.area_id,
              ];
              this.params.cityList = this.defaultArea.join(",");
              this.params.province_id = this.defaultArea[0];
              this.params.city_id = this.defaultArea[1];
              this.params.area_id = this.defaultArea[2];
            } else {
              this.params.cityList = "";
              this.params.province_id = 0;
              this.params.city_id = 0;
              this.params.area_id = 0;
              this.defaultArea = [0];
            }
          }
          let obj = {
            consignee_name: res.data.data.principal_name,
            consignee_mobile: res.data.data.principal_mobile,
            address: res.data.data.address,
          };

          let paramsObj = Object.assign({}, this.params, obj);
          this.$set(this, "params", paramsObj);
          // getDepartmentListCommon(this, this.params);
        });
    },
  },
  computed: {
    ...mapGetters(["auditreeoutAdd_Get", "auditreeoutIndex_Get"]),
  },
  mounted() {
    // getCompanyListCommon(this); //获取组织
    if (this.auditreeoutAdd_Get.reborn_outstock_id) {
      this.$axMethod
        .getRebornOutstockInfo({
          reborn_outstock_id: this.auditreeoutAdd_Get.reborn_outstock_id,
        })
        .then((res) => {
          if (res.data.code == 200) {
            for (const obj of res.data.data.goods_list) {
              obj.wareHouselist = [
                {
                  warehouse_id: obj.warehouse_id,
                  warehouse_name: obj.warehouse_name,
                  allocation_id: obj.allocation_id,
                  allocation_name: obj.allocation_name,
                },
              ];
              obj.warehouseObj = {
                warehouse_id: obj.warehouse_id,
                warehouse_name: obj.warehouse_name,
                allocation_id: obj.allocation_id,
                allocation_name: obj.allocation_name,
              };
            }
            this.params = res.data.data.info;
            this.TableData = res.data.data.goods_list;

            this.defaultArea = [
              res.data.data.info.province_id,
              res.data.data.info.city_id,
              res.data.data.info.area_id,
            ];
            // bindIdsF(this, res.data.data.info, [
            //   ["supplier_id", "supplier_name", "supplierList"],
            //   ["department_id", "department_name", "departmentList", "options"]
            // ]);
            opthionList(this, res.data.data.info);
            this.params.cityList = this.defaultArea.join(",");
            this.params.province_id = this.defaultArea[0];
            this.params.city_id = this.defaultArea[1];
            this.params.area_id = this.defaultArea[2];
          }
        });
    }
    // this.companyChange(); //获取部门 联动获取客户的下拉

    let { employee_id, company_admin_id } = localStorage;
    if ([0, 1, 2].includes(Number(employee_id)) || company_admin_id > 0) {
      this.$set(this, "params", {
        business_time: this.$publicFution.formatData(new Date()),
        company_id: Number(localStorage.company_id),
        company_name: localStorage.company_name,
      });
    } else {
      this.$set(this, "params", {
        business_time: this.$publicFution.formatData(new Date()),
        company_id: Number(localStorage.company_id),
        department_id: Number(localStorage.department_id),
        company_name: localStorage.company_name,
      });
    }
    opthionList(this, localStorage);
    // this.companyList[{"company_id": this.params.company_id,"company_name": company_name}]
    // console.log(this.params)
  },
};
</script>

<style scoped>
</style>
